.wrap-table {
    .table {
        .table-header {
            background-color: #e9e9e9;

            .column {
                .column-content {
                    margin-right: 5px;
                }

                .sorter-icon {
                    display: inline-flex;
                    flex-direction: column;

                    .active-sorter-icon {
                        border-bottom-color: #252525;
                        border-top-color: #252525;
                    }
                }
            }

            .active-header-sorter {
                background-color: #d6d6d6;
            }
        }

        .active-column-sorter {
            background-color: #e9e9e9;
        }
    }

    .pagination {
        .page {
            padding: 10px;
            cursor: pointer;
            border: 1px solid;
        }

        .disable {
            cursor: not-allowed;
        }

        .active-current-page {
            background-color: #7f7f7f;
            color: #fff;
        }
    }

    .triangle-up {
        width: 0;
        height: 0;
        margin-bottom: 1px;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-bottom: 6px solid rgb(146, 146, 146);
    }

    .triangle-down {
        width: 0;
        height: 0;
        margin-top: 1px;
        border-left: 4px solid transparent;
        border-right: 4px solid transparent;
        border-top: 6px solid rgb(146, 146, 146);
    }
}